@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rubik:wght@300&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Rubik', sans-serif;
}

.container {
  height: 100vh;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: whitesmoke;
}
.card {
    width: 450px;
    background-color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
}
input {
    width: 100%;
    height: 60px;
    text-align: center;
    font-size: 16px;
    font-family: 'Rubik', sans-serif;
    border: none;
    background-color: rgb(236, 236, 236);
}

input:focus {
    outline: none;
}

::-webkit-input-placeholder {
    color: dimgrey;
}

:-ms-input-placeholder {
    color: dimgrey;
}

::placeholder {
    color: dimgrey;
}
.profile {
    padding: 50px;
    color:#202020;
}

.profile > span {
    font-family: 'Lato', sans-serif;
    font-size: 32px;
}

.profile > h1 {
    font-family: 'Lato', sans-serif;
    font-size: 32px;
    color: rgb(8, 51, 95);
}

.profile > img {
    border-radius: 50%;
    height: 100px;
    width: 100px;
}

.profile > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
}

.profile > div > div {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.profile > div > div > p {
    margin-left: 10px;
}

.stats {
    display: flex;
    flex-direction: row;
    background-color: rgb(8, 51, 95);
    width: 100%;
    justify-content: center;
    color: white;
    font-family: 'Rubik', sans-serif;
    padding: 30px;
}

.stats > div {
    margin: 10px;
}

a {
    text-decoration: none;
    color: #202020;
}
